<template>
	<div class="emb-product-add">
		<v-row>
			<v-col cols="12" sm="9" md="10" lg="10" class="mx-auto">
				<v-row>
					<v-col cols="12" sm="12" md="6" lg="6">
						<div class="product-images-wrap d-flex">
							<div class="product-images mr-4">
								<div class="thumb-wrap" for="upload">
									<v-img :src="fileOne" alt="images" height="70" />
									<div class="edit-btn d-flex justify-center align-center">
										<v-icon dark>add</v-icon>
									</div>
									<input type="file" @change="selectpic" ref="pic1" id="upload" accept="image/*"
										class="upload-img">
								</div>
								<div class="thumb-wrap" for="upload">
									<v-img :src="filetwo" height="70" />
									<div class="edit-btn d-flex justify-center align-center">
										<v-icon dark>add</v-icon>
									</div>
									<input type="file" @change="selectpict" ref="pic2" id="upload" accept="image/*"
										class="upload-img">
								</div>
								<div class="thumb-wrap" for="upload">
									<v-img :src="filethree" height="70" />
									<div class="edit-btn d-flex justify-center align-center">
										<v-icon dark>add</v-icon>
									</div>
									<input type="file" @change="selectpich" ref="pic3" id="upload" accept="image/*"
										class="upload-img">
								</div>
								<div class="thumb-wrap" for="upload">
									<v-img :src="filefour" height="70" />
									<div class="edit-btn d-flex justify-center align-center">
										<v-icon dark>add</v-icon>
									</div>
									<input type="file" @change="selectpicf" ref="pic4" id="upload" accept="image/*"
										class="upload-img">
								</div>
							</div>
							<div class="product-preview-wrap">
								<div id="mainPic">
									<img  :src="fileOne" style="width: 100%;" />
								</div>							</div>
						</div>
					</v-col>
					<v-col cols="12" sm="12" md="6" lg="6" class="content-wrap pl-md-6">
						<router-link class="pt-4 d-block font-weight-medium" to="/admin-panel/products">Back to Products
						</router-link>

						<v-text-field prepend-icon="add" v-model="pName.en" label="Name in english"></v-text-field>
						<v-text-field prepend-icon="add" v-model="pName.kiny" label="Name in kinyawanda"></v-text-field>
						<v-text-field prepend-icon="add" v-model="pName.sw" label="Name in swahili"></v-text-field>
						<v-text-field prepend-icon="add" v-model="pName.fr" label="Name in french"></v-text-field>
						<v-select prepend-icon="add" :items="Departments" label="Departments" @change="fetchCategory"
							v-model="assignedDepartments" dense></v-select>
						<v-select prepend-icon="add" :items="selectablecategories" label="Categories"
							@change="fetchcategories" v-model="selectedcategory" dense></v-select>
						<v-select prepend-icon="add" :items="selectedCompany" label="Company" @change="fetchSelected"
							v-model="selected" dense></v-select>
						<v-text-field prepend-icon="add" v-model="price" class="price-input" placeholder="Add Price">
						</v-text-field>
						<v-text-field prepend-icon="add" v-model="description.en" label="Description in english">
						</v-text-field>
						<v-text-field prepend-icon="add" v-model="description.kiny" label="Description in kinyawanda">
						</v-text-field>
						<v-text-field prepend-icon="add" v-model="description.sw" label="Description in swahili">
						</v-text-field>
						<v-text-field prepend-icon="add" v-model="description.fr" label="Description in french">
						</v-text-field>
						<v-dialog v-model="service" max-width="800">
		                    <v-card class="content-wrap pl-md-6 py-6 px-4">
								<div class="title">Service:</div>
								<div v-for="n in serviceList" :key="n" class="servicesList">
									<div class="servicetext ml-10 font-weight-medium">
										{{n.name}}
									</div>
									<div class="money ml-10 accent--text font-weight-medium"><emb-currency-sign></emb-currency-sign> {{n.price}}</div>
									<div class="ml-10">
										<v-btn @click="deleteItem(n)" icon ><v-icon class="red--text">delete</v-icon></v-btn>
									</div>
								</div>
								<div class="service">
									<div id="outline">
										<v-text-field  id="outline" prepend-icon="add" v-model="details" label="Service Details"></v-text-field>
									</div>
									<v-col class="ml-2" cols="6" sm="3">
										<v-text-field v-on:keyup.enter="saveService" prepend-icon="add" v-model="priceservice" class="accent--text price-input" placeholder="Price"></v-text-field>
									</v-col>
									<div class="mt-6 ">
										<v-btn color="accent"  @click="saveService">Add</v-btn>
									</div>
								</div>
								<v-card-actions class="layout justify-center">
									<!-- <v-btn color="primary" @click="deleteService">Cancel</v-btn> -->
									<v-btn color="primary"  @click="service = false">Close</v-btn>
								</v-card-actions>
							</v-card>
						</v-dialog>
						<div class="mb-4">
							<v-btn color="accent" @click="addService" class="mx-3" large>Additional service</v-btn>
						</div>
						<v-btn color="accent" @click="uploadProduct" class="mx-3" large>Create</v-btn>
						<v-btn color="white" large>Discard</v-btn>
					</v-col>
				</v-row>
			</v-col>
		</v-row>
	</div>
</template>
<style  scoped>
/* @media screen and (min-width: width 1000px){ */
	.product-preview-wrap  img{
	  /* height: 450px;
	  width: 10px; */
	  height: 100%;
	  width: 100%;
	}
/* } */
.service{
	display: flex;
	text-align: center;
}
#outline{
	position: relative;
	top: 10px;
	width: 500px;
	/* left: 20px; */
}
.servicesList{
	display: flex;
	align-items: center;
}
.servicetext{
	width: 400px;
	margin-bottom: 20px;
}
.money{
	width: 200px;
}
</style>
<script>
	import department from "Api/department";
	import Company from "Api/company";
	import Categories from "Api/categories";
	import heloProducts from "Api/products";
	export default {
		data() {
			return {
				fileOne: 'https://via.placeholder.com/625x800',
				filetwo: 'https://via.placeholder.com/625x800',
				filethree: 'https://via.placeholder.com/625x800',
				filefour: 'https://via.placeholder.com/625x800',
				pName: {
					en: '',
					sw: '',
					kiny: '',
					fr: '',
				},
				price: '',
				assignedDepartments: '',
				data: '',
				assignedDepartmentsId: '',
				Departments: [],
				allcategories: [],
				selectablecategories: [],
				selectedcategory: '',
				selectedCompany: [],
				allCompany: '',
				assignedCategoriesId: '',
				selected: '',
				selectedid: '',
				description: {
					en: '',
					sw: '',
					kiny: '',
					fr: ''
				},
				prictures: {
					pic1: '',
					pic2: '',
					pic3: '',
					pic4: ''
				},
				service: false,
				priceservice: '',
				serviceNumber:1,
				serviceList: [],
				// servicePrice: '',
				details:''
			}
		},
		methods: {
			deleteItem(item) {
				const index =this.serviceList.indexOf(item)
				this.serviceList.splice(index)
				console.log(item)
			},
			addService() {
				this.service= true
			},
			deleteService() {
				this.serviceList= []
				this.service= false
			},
			saveService() {
				// console.log(this.$refs.price)
				// console.log('hello')
				this.serviceList.push({
					name:this.details,
					price:this.priceservice,
				})
				this.details= '',
				this.priceservice= ''
			},
			fetchCategory() {
				this.selectedcategory= ''
				this.data.forEach(el => {
					if (el.name.kiny === this.assignedDepartments) {
						this.assignedDepartmentsId = el._id
					}
				});
				this.allcategories.data.data.forEach(el => {
					if (el.department === this.assignedDepartmentsId) {
						//    console.log(el.name.kiny)
						this.selectablecategories.push(el.name.kiny)
					}
				});
			},
			fetchSelected() {
				this.allCompany.data.data.forEach(el => {
					if (el.name === this.selected) {
						this.selectedid = el._id
						console.log(this.selectedid)
					}
				})
			},
			fetchcategories() {
				this.allcategories.data.data.forEach(el => {
					if (el.name.kiny === this.selectedcategory) {
						this.assignedCategoriesId = el._id
						console.log(this.assignedCategoriesId)
						//    console.log(el.name.kiny)
						//    this.selectablecategories.push(el.name.kiny)
					}
				});
			},
			selectpic(e) {
				const file = e.target.files[0]
				const reader = new FileReader();
				reader.onload = (e) => {
					this.fileOne = e.target.result;
					this.pictures.pic1 = this.fileOne;
				}

				if (file) {
					reader.readAsDataURL(file);

				}

			},
			selectpict(e) {

				const file = e.target.files[0]
				const reader = new FileReader();
				reader.onload = (e) => {
					this.filetwo = e.target.result;
					this.pictures.pic1 = this.filetwo;
				}

				if (file) {
					reader.readAsDataURL(file);

				}

			},
			selectpich(e) {
				const file = e.target.files[0]
				const reader = new FileReader();
				reader.onload = (e) => {
					this.filethree = e.target.result;
					this.pictures.pic1 = this.filethree;
				}

				if (file) {
					reader.readAsDataURL(file);

				}

			},
			selectpicf(e) {
				const file = e.target.files[0]
				const reader = new FileReader();
				reader.onload = (e) => {
					this.filefour = e.target.result;
					this.pictures.pic1 = this.filefour;
				}

				if (file) {
					reader.readAsDataURL(file);

				}

			},
			async uploadProduct() {
				console.log("description",JSON.stringify(this.description))
				try {
					let container = new FormData();
					container.append('description', JSON.stringify(this.description))
					container.append('department', this.assignedDepartmentsId)
					container.append('category', this.assignedCategoriesId)
					container.append('company', this.selectedid)
					container.append('name', JSON.stringify(this.pName))
					container.append('price', this.price)
					container.append('picture1', this.$refs.pic1.files[0])
					container.append('picture2', this.$refs.pic2.files[0])
					container.append('picture3', this.$refs.pic3.files[0])
					container.append('picture4', this.$refs.pic4.files[0]),
					container.append('additionalServices',JSON.stringify(this.serviceList))
					console.log(...container);
					console.log(this.serviceList)
					const resProcts = await heloProducts.uploadProduct(container)
					this.$snotify.success(`${resProcts.data.message}`,{
                    closeOnClick: false,
                    pauseOnHover: false,
                    timeout: 1000,
					showProgressBar:false,
				});
				this.router.push('/admin-panel/products')
				} catch (err) {
					// console.log()
					this.$snotify.error(err.response.data.message,{
                    closeOnClick: false,
                    pauseOnHover: false,
                    timeout: 8000,
					showProgressBar:false,
				});
				}

			}
		},
		async mounted() {
			try {
				const res = await department.getDepartment()
				this.data = await res.data.data
				this.data.forEach(el => {
					this.Departments.push(el.name.kiny)
				});
				this.allcategories = await Categories.getCategories()
				this.allCompany = await Company.getCompany()
				// this.selectedCompany.push(this.allCompany.data.data)
				// console.log(this.selectedCompany)
				this.allCompany.data.data.forEach(el => {
					// console.log(el.name)
					this.selectedCompany.push(el.name)
				})
			} catch (err) {
				console.log(err.message)
			}
		}
	}
</script>